.slider {
    background: white;
    box-shadow: 0 10px 20px -5px rgba(0, 0, 0, .125);
    height: 130px;
    margin: auto;
    overflow: hidden;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 100vw;
  }
  
  .slider::before,
  .slider::after {
    background: linear-gradient(to right, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 0) 100%);
    content: "";
    height: 100px;
    position: absolute;
    width: 200px;
    z-index: 2;
  }
  
  .slider::after {
    right: 0;
    top: 0;
    transform: rotateZ(180deg);
  }
  
  .slider::before {
    left: 0;
    top: 0;
  }
  
  .slide-track {
    animation: scroll 40s linear infinite; /* Use the animation defined in @keyframes scroll */
    display: flex;
    width: calc(250px * 14); /* Adjust width based on number of images */
  }
  
  .slide {
    height: 100px;
    width: 250px;
  }
  
  .slide img {
    margin-top: 1rem;
    height: 100px;
    width: auto;
  }
  
  @keyframes scroll {
    0% {
      transform: translateX(0);
    }
    100% {
      transform: translateX(calc(-250px * 14)); /* Adjust based on number of images */
    }
  }
  